
















import { Component, Vue, Watch } from 'vue-property-decorator'

import { ArScene, Loader, IndexFile, IDBFS, ArRender } from '@basic'
import { scenes } from '@scenes'

import AppSet from '@/AppSet'
import DblClickPrev from '@/DblClickPrev'

import DetailPanel from '@/components/DetailPanel.vue'
import Loading from '@/components/Loading.vue'
import MainSetPanel from '@/components/MainSetPanel.vue'

@Component({
  components: {
    DetailPanel,
    Loading,
    MainSetPanel
  }
})
export default class App extends Vue {
  private detailOpened = false
  private loading = true
  private scene = 'none'
  private setOpened = false
  private readonly resUrl = '/resources'
  // private readonly resUrl = 'http://172.16.0.10:8088'
  private render?: ArRender

  get appClass() {
    let { themeColor, themePanel, trans } = Vue.$sets
    return [themeColor, themePanel, trans]
  }

  async initAR(dom: HTMLElement, msgTo: Loading) {
    if (this.render) return
    this.loading = true
    IDBFS.resServer = this.resUrl
    Loader.processCB = ev => {
      let { loaded, total, msg } = ev
      msgTo.info = msg
      msgTo.loaded = loaded
      msgTo.total = total
      console.log(`(${loaded}/${total}) ${msg}`)
    }
    await Loader.updateCache()
    await Loader.loadScenes(scenes)
    this.render = await Loader.initAR(dom)
    await Loader.startAR()
    this.loading = false
  }

  async mounted() {
    let compLoading = this.$refs.Loading as Loading
    let compDetailPanel = this.$refs.DetailPanel as DetailPanel
    let domCam = document.getElementById("cam")!
    await this.initAR(domCam, compLoading)
    this.render!.events.on("sceneChanged", (scene?: ArScene) =>
      this.scene = (scene && scene.sceneID) || 'none'
    )
    DblClickPrev.prev(document.body)
    compDetailPanel.updateScene()
  }

  @Watch("detailOpened") detailOpenedChanged(opened: boolean) {
    opened ? this.render?.hide() : this.render?.start()
  }
}
