import { distance } from '@basic'

class DblClickPrev {
  lastTouchEnd!: number
  lastX!: number
  lastY!: number
  reset() {
    this.lastTouchEnd = 0
    this.lastX = -100
    this.lastY = -100
  }
  constructor() {
    this.reset()
  }
  prevent(event: TouchEvent) {
    let now = performance.now()
    if (
      event.cancelable &&
      now - this.lastTouchEnd < 30 &&
      distance(
        event.changedTouches[0].clientX,
        event.changedTouches[0].clientY,
        this.lastX,
        this.lastY
      ) < 24
    ) {
      event.preventDefault()
      this.reset()
    } else {
      this.lastTouchEnd = now
      this.lastX = event.changedTouches[0].clientX
      this.lastY = event.changedTouches[0].clientY
    };
  }

  static prev(elem: HTMLElement) {
    let _prev = new DblClickPrev()
    elem.addEventListener("touchend", _prev.prevent.bind(_prev))
  }
}

export default DblClickPrev