





import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

import AppSet from '@/AppSet'

@Component
export default class ExtendPanel extends Vue {
  @Prop({ default: false }) private hide!: boolean
  @Prop({ default: false }) private value!: boolean

  afterClosed = true
  afterOpened = false

  private timer = 0

  get animTime() {
    return {
      "ease-trans": 600,
      "easy-trans": 200,
      "none-trans": 100
    }[Vue.$sets.trans]
  }

  get opened() {
    return this.value
  }

  get panelClass() {
    return { opened: this.value, hide: this.hide }
  }

  @Watch("value") openedWatcher(open: boolean) {
    if (this.timer) {
      clearTimeout(this.timer)
      this.timer = 0
    }
    if (open) {
      this.afterClosed = false
      this.timer = setTimeout(() => {
        this.afterOpened = true
      }, this.animTime)
    } else {
      this.afterOpened = false
      this.timer = setTimeout(() => {
        this.afterClosed = true
      }, this.animTime)
    }
  }

  close() {
    this.$emit("input", false)
  }

  open() {
    this.$emit("input", true)
  }

  toggle() {
    this.opened ? this.close() : this.open()
  }

  mounted() {
    this.afterClosed = !this.value
    this.afterOpened = this.value
  }
}
