





import { Component, Prop, Vue, Provide } from 'vue-property-decorator'

@Component
export default class SelectGroup extends Vue {
  @Prop() protected value!: any

  @Provide() isActive(name: any) {
    return this.value === name
  }

  changeTo(name: any) {
    this.$emit("input", name)
  }

  clicked(event: Event) {
    let target = event.srcElement as HTMLElement | null
    let path: Array<HTMLElement> = []
    while (target && target !== this.$el) {
      path.unshift(target)
      target = target.parentElement
    }
    for (let elem of path) {
      let itemName = (<any>elem).__vue__?.name
      if (itemName) {
        this.$emit("input", itemName)
        return
      }
    }
  }
}
