import './registerServiceWorker'

import { library } from '@fortawesome/fontawesome-svg-core'
import {
  faTimes,        // MainHelper, MainSetPanel
  faCog,          // MainSetPanel, ...set...
  faCogs,         // ModelBox
  faSlidersH,     // ModelBox
  faInfoCircle,   // ModelBox
  faEye,          // ModelBox
  faCrosshairs,   // ModelBox
  faQuestionCircle, // DetailBox
  faAnchor,       // DetailBox
  faVideo,        // DetailBox
  faChartLine,    // DetailBox

  // faTools,

} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(
  faTimes,
  faCog,
  faCogs,
  faSlidersH,
  faInfoCircle,
  faEye,
  faCrosshairs,
  faQuestionCircle,
  faAnchor,
  faVideo,
  faChartLine,
  // faTools
)

import Vue from 'vue'

import { IndexedDB } from '@basic'
import App from './App.vue'
import AppSet from './AppSet'

async function initApp() {

  Vue.component('fa-icon', FontAwesomeIcon)
  Vue.config.productionTip = false

  let tables: IDBTables = new Map([
    ['sets',
      {
        param: { keyPath: 'key' }
      }
    ]
  ])

  Vue.$db = await IndexedDB.open(
    'cbkzxt-ar',
    200509,
    function (req) {
      IndexedDB.createFromTables(req.result, tables)
    }
  )

  Vue.$sets = new Vue({
    data: {
      sets: AppSet.createAndLinkTo(Vue.$db)
    }
  }).sets

  new Vue({
    render(h) { return h(App) }
  }).$mount('#app')

}

initApp()