import { newEaseFunc } from '@basic'

class Inertia {
  private _value1 = 0
  private _time1 = -100
  private _value2 = 0
  private _time2 = -100
  private _freeV = 0
  private _velocity = 0
  private _looper = 0

  dampingTime = 500
  exertingCallback?: (value: number) => void
  stoppedCallback?: (value: number) => void
  threshold = {
    betweenChange: 400,
    beforeFree: 40,
    velocityMin: 40,
  }

  get value() {
    return this._value2
  }

  set value(val: number) {
    this.changeValue(val)
  }

  get velocity() {
    return this._velocity
  }

  private _keepLoop() {
    this._looper = requestAnimationFrame(this._loop.bind(this))
  }

  private static _ease = newEaseFunc(0.15)

  private _loop(t: DOMHighResTimeStamp) {
    let dt = t - this._time2
    this._velocity = this._freeV * (1 - Inertia._ease(dt / this.dampingTime))
    if (dt >= this.dampingTime || !this._velocity) {
      this._looper = 0
      return this.stop()
    }
    this._value2 += this._velocity * dt
    this.exertingCallback && this.exertingCallback(this._value2)
    this._keepLoop()
  }

  stop() {
    if (this._looper) {
      cancelAnimationFrame(this._looper)
      this._looper = 0
    }
    this._velocity = 0
    this.stoppedCallback && this.stoppedCallback(this._value2)
  }

  changeValue(value: number, time?: number) {
    this._time1 = this._time2
    this._value1 = this._value2
    this._time2 = time || performance.now()
    this._value2 = value
    let dt = this._time2 - this._time1
    this._velocity = dt > this.threshold.betweenChange ?
      0 : (this._value2 - this._value1) / dt
  }

  free(time?: number) {
    this._freeV = this._velocity
    if (!time) time = performance.now()
    let dtBeforeFree = time - this._time2
    if (dtBeforeFree > this.threshold.beforeFree)
      return this.stop()
    this._time2 = performance.now()
    this._keepLoop()
  }
}

export default Inertia