












import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

import {
  ArVideoScene
} from '@basic'

import { Loader } from '@basic'
import TabedBox from './TabedBox.vue'
import SelectItem from './SelectItem.vue'

@Component({
  components: {
    SelectItem,
    TabedBox,
  }
})
export default class ModelBox extends Vue {
  @Prop({ default: '' }) scene!: string
  nowTab: "video" | "desc" | "set" = "video"
  video = ''

  nowScene?: ArVideoScene

  @Watch('scene') sceneChanged() {
    this.nowScene = Loader.scenes.get(this.scene) as ArVideoScene
    this.video = this.nowScene.videoUrl
  }

  mounted() {
    this.sceneChanged()
  }
}
