




































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

import {
  ArScene, ModelRender, OrbitController,
  ModelViewer, ArModelScene, RangeDesc,
  OrbitValue
} from '@basic'


import THREE from 'three'

import AppSet from '@/AppSet'
import { Loader } from '@basic'

import SelectItem from './SelectItem.vue'
import SliderGroup from './SliderGroup.vue'
import TabedBox from './TabedBox.vue'
import TickSlider from './TickSlider.vue'
import ValueBubble from './ValueBubble.vue'

@Component({
  components: {
    ModelViewer,
    SelectItem,
    SliderGroup,
    TabedBox,
    TickSlider,
    ValueBubble
  }
})
export default class ModelBox extends Vue {
  @Prop({ default: '' }) scene!: string
  nowScene?: ArModelScene
  params: arSceneParam[] = []
  paramsValue: { [key: string]: any } = {}

  nowTab: "view" | "control" | "desc" | "set" = "view"

  private nowViewControl: "rotateY" | "rotateX" | "zoom" = "rotateY"
  private nowParam: arSceneParam | null = null
  // private sliderComp!: TickSlider
  private viewerComp!: ModelViewer
  private sliderRange = new RangeDesc()
  private sliderGroupValue: any = ''
  private sliderValue = 0
  private tickSliding = false
  private viewIsInitValue = true

  private get isCustomPage() {
    return this.nowTab !== "desc" && this.nowTab !== "set"
  }

  private get appSet() {
    return Vue.$sets
  }

  isSliderGroup(desc: RangeDesc | {
    cases: Map<string, any>;
    default: string;
  }): desc is {
    cases: Map<string, any>;
    default: string;
  } {
    return (<any>desc).cases
  }

  private get showSliderGroup() {
    return this.nowTab === 'control' &&
      this.nowParam && this.isSliderGroup(this.nowParam.desc)
  }

  private get cases(): string[] {
    if (!this.nowParam || !this.isSliderGroup(this.nowParam.desc))
      return []
    return Array.from(this.nowParam.desc.cases.keys())
  }

  mounted() {
    // this.sliderComp = this.$refs["slider"] as TickSlider
    this.viewerComp = this.$refs['model-viewer'] as ModelViewer
    this.$watch('viewerComp.viewController.cameraPosition',
      function () {
        this.viewIsInitValue = this.viewerComp.viewController.isInitValue
        this.updateSlider()
      }
    )
    this.sceneChanged()
    this.updateSlider()
  }

  @Watch("nowTab") nowTabChange() {
    if (this.nowTab === 'control' && !this.nowParam)
      this.nowParam = this.params[0]
    this.updateSlider()
  }

  @Watch("nowViewControl") nowViewControlChange() {
    this.updateSlider()
  }

  @Watch("nowParam") nowParamChange() {
    this.updateSlider()
  }

  @Watch('scene') sceneChanged() {
    this.nowScene = Loader.scenes.get(this.scene) as ArModelScene
    this.viewerComp.useScene(this.nowScene)
    if (this.nowScene) {
      this.params = this.nowScene.params
      this.paramsValue = this.nowScene.paramsValue.values
    }
  }

  @Watch("sliderValue") sliderValueChange() {
    if (this.nowTab === 'view')
      this.viewerComp.viewController[this.nowViewControl]
        = this.sliderValue
    else if (this.nowTab === 'control' && this.nowParam)
      this.paramsValue[this.nowParam.name] = this.sliderValue
  }

  @Watch("sliderGroupValue") sliderGroupValueChange() {
    if (this.nowTab === 'control' && this.nowParam)
      this.paramsValue[this.nowParam.name] = this.sliderGroupValue
  }

  bubbleDbClick() {
    this.sliderValue = OrbitValue.ranges[this.nowViewControl].initValue
  }

  pause() {
    this.viewerComp.modelRender?.pause()
  }

  resetView() {
    this.viewerComp.viewController.reset()
    this.updateSlider()
  }

  slidingChange(val: boolean) {
    this.tickSliding = val
  }

  start() {
    this.viewerComp.modelRender?.start()
  }

  updateSlider() {
    if (this.nowTab === 'view') {
      this.sliderRange = OrbitValue.ranges[this.nowViewControl]
      this.sliderValue = this.viewerComp.viewController[this.nowViewControl]
    } else if (this.nowTab === 'control' && this.nowParam) {
      let { desc } = this.nowParam
      if (this.isSliderGroup(desc))
        return this.sliderGroupValue = this.paramsValue[this.nowParam.name]
      this.sliderRange = desc
      this.sliderValue = this.paramsValue[this.nowParam.name]
    }
  }
}
