














































import { Component, Prop, Watch, Vue } from 'vue-property-decorator'
import { VueConstructor } from 'vue'

import ExtendPanel from './ExtendPanel.vue'

import { ArScene, ArSceneType, Loader } from '@basic'
import detailTexts from '../detail-texts'

import NoneBox from './NoneBox.vue'
import ModelBox from './ModelBox.vue'
import VideoBox from './VideoBox.vue'

@Component({
  components: {
    NoneBox,
    ModelBox,
    VideoBox,
  }
})
export default class DetailPanel extends ExtendPanel {
  @Prop({ default: '' }) private scene!: string


  info = detailTexts[ArSceneType.none]
  title = ''
  type = 'none'

  @Watch('scene') updateScene() {
    let scene = Loader.scenes.get(this.scene)
      || DetailPanel.defaultNone
    this.title = scene.title
    this.type = ArSceneType[scene.sceneType]
    this.info = detailTexts[scene.sceneType]
  }

  mounted() {
    this.updateScene()
  }

  static defaultNone = new ArScene()
}
