import Vue from 'vue'
import { IndexedDB } from '@basic'

class AppSet {
  themeColor: "dark" | "system" | "light" = "system"
  themePanel: "trans" | "glass" | "untrans" = "glass"
  trans: "ease-trans" | "easy-trans" | "none-trans" = "ease-trans"
  mat: "origin" | "normal" | "color" = "origin"
  light: "on" | "easy" | "off" = "on"
  shadow: "on" | "off" = "on"

  async saveToDb(db: IndexedDB, key?: string, value?: any) {
    if (key) {
      if (!value) value = (this as any)[key]
      await db.put('sets', { key, value })
    }
    else {
      let props = Object.getOwnPropertyNames(this)
      for (let key of props) await this.saveToDb(db, key)
    }
  }

  async loadFromDB(db: IndexedDB, key?: string) {
    if (key) {
      let result = await db.get('sets', key)
      if (!result) return
      (<any>this)[key] = result.value
      return result
    }
    else {
      let props = Object.getOwnPropertyNames(this)
      for (let key of props) await this.loadFromDB(db, key)
      return this
    }
  }

  static createAndLinkTo(db: IndexedDB) {
    let obj = new AppSet
    obj.loadFromDB(db)
    return new Proxy(obj, {
      set: function (obj, prop: string, value) {
        (<any>obj)[prop] = value
        obj.saveToDb(db, prop, value)
        return true
      }
    })
  }
}

export default AppSet