








import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class ValueBubble extends Vue {
  @Prop({ default: false }) private big!: boolean
  @Prop({ default: 0 }) private fix!: number
  @Prop({ default: "" }) private unit!: string
  @Prop({ default: 0 }) private value!: number

  waitDbClick = 0;

  get vtext() {
    return this.value.toFixed(this.fix) + this.unit
  }

  cleanDbClick() {
    clearTimeout(this.waitDbClick)
    this.waitDbClick = 0
  }

  touched(event: MouseEvent) {
    event.preventDefault()
    if (this.waitDbClick) {
      this.cleanDbClick()
      this.$emit("dbclick")
    } else {
      this.waitDbClick = setTimeout(this.cleanDbClick.bind(this), 300)
    }
  }
}
