



















import { Component, Prop, Vue } from 'vue-property-decorator'

import { stick, RangeDesc } from '@basic'

import Inertia from '@/Inertia'

@Component
export default class TickSlider extends Vue {
  @Prop({ default: () => RangeDesc.create() }) private range!: RangeDesc
  @Prop({ default: 8 }) private tickWidth!: number
  @Prop({ default: 0 }) private value!: number

  private sliding = false
  private svalue = 0

  private cDiv2 = 0
  private startValue = 0
  private startX = 0

  private inertia = new Inertia()

  get beginTick() {
    return Math.floor(
      (this.value - this.range.min) / this.range.step -
      this.cDiv2 / this.tickWidth
    )
  }

  get showTickNum() {
    return Math.ceil(this.cDiv2 / this.tickWidth) * 2
  }

  get tickNum() {
    return Math.floor(this.range.distance / this.range.step) + 1
  }

  get transX() {
    if (!this.sliding) return 0
    let x = ((this.value - this.svalue) / this.range.step) * this.tickWidth
    return this.range.contain(this.svalue) ? x : stick(x)
  }

  private _moved(x: number) {
    let delta = x - this.startX
    this.svalue = this.startValue - (delta / this.tickWidth) * this.range.step
    let fv = this.range.fixValue(this.svalue)
    if (fv != this.value) this.$emit("input", fv)
  }

  mounted() {
    this.onResize()
  }

  onResize() {
    this.cDiv2 = this.$el.clientWidth / 2
  }

  tickClass(n: number) {
    let nn = n + this.beginTick
    return {
      hidden: nn < 1 || nn > this.tickNum,
      big: nn % this.range.group === 1
    }
  }

  touchStarted(event: TouchEvent) {
    if (!this.sliding) {
      this.svalue = this.value
      this.startValue = this.value
      this.sliding = true
    } else this.startValue = this.svalue

    this.startX = event.touches[0].clientX

    this.inertia.stoppedCallback = undefined
    this.inertia.exertingCallback = undefined
    this.inertia.stop()
    this.inertia.changeValue(this.startX)

    this.$emit("sliding", true)
  }

  touchMoved(event: TouchEvent) {
    event.preventDefault()
    let v = event.touches[0].clientX
    this.inertia.changeValue(v)
    this._moved(v)
  }

  touchEnded(event: TouchEvent) {
    this.inertia.stoppedCallback = () => {
      this.sliding = false
      this.$emit("sliding", false)
    }
    this.inertia.exertingCallback = this._moved.bind(this)
    this.inertia.free()
  }
}
