

















import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

import { ArScene, Loader } from '@basic'

import DescBox from './DescBox.vue'
import SelectGroup from './SelectGroup.vue'
import SelectItem from './SelectItem.vue'
import SetPanel from './SetPanel.vue'
import { VueConstructor } from 'vue'

@Component({
  components: {
    DescBox,
    SelectGroup,
    SelectItem,
    SetPanel
  }
})
export default class TabedBox extends Vue {

  static defaultNone = new ArScene()

  @Prop() private scene!: string
  @Prop({ default: 'desc' }) private value!: string
  desc: VueConstructor<Vue> | null = null

  @Watch('scene') updateScene() {
    let scene = Loader.scenes.get(this.scene) || TabedBox.defaultNone
    this.desc = scene.desc || null
  }

  mounted() {
    this.updateScene()
  }

}
