












import { Component, Prop, Provide, Vue, Watch } from 'vue-property-decorator'

import SelectGroup from './SelectGroup.vue'

@Component
export default class SliderGroup extends SelectGroup {
  @Prop({ default: 96 }) private itemWidth!: number

  activeIndex = 0
  touching = false

  private startX = 0

  get helperWidthAfter() {
    return this.activeIndex * this.itemWidth + "px"
  }

  get helperWidthBefore() {
    return (
      (this.$children.length - 1 - this.activeIndex) * this.itemWidth + "px"
    )
  }

  mounted() {
    this.updateActiveIndex()
  }

  @Watch("$children") childrenChange() {
    this.updateActiveIndex()
  }

  @Watch("value") valueChange() {
    this.updateActiveIndex()
  }

  toBefore() {
    if (this.activeIndex > 0)
      this.changeTo((<any>this.$children[this.activeIndex - 1]).name)
  }

  toNext() {
    if (this.activeIndex < this.$children.length - 1)
      this.changeTo((<any>this.$children[this.activeIndex + 1]).name)
  }

  touchStarted(event: TouchEvent) {
    if (event.cancelable) event.preventDefault()
    this.touching = true
    this.startX = event.touches[0].clientX
  }

  touchMoved(event: TouchEvent) {
    if (event.cancelable) event.preventDefault()
    if (!this.touching) return
    let deltaX = event.touches[0].clientX - this.startX
    if (Math.abs(deltaX) > 24) {
      this.touching = false
      deltaX > 0 ? this.toBefore() : this.toNext()
    }
  }

  touchEnded(event: TouchEvent) {
    if (event.cancelable) event.preventDefault()
    if (this.touching) {
      this.touching = false
      this.clicked(event)
    }
  }

  updateActiveIndex() {
    this.activeIndex = this.$children.findIndex(
      elem => (<any>elem).name === this.value
    )
  }
}
